body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-color{
  background: #006491;
}
.form-box{
  border-radius: 10px;
}
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=tel]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]){
  border-color: #808080;
  box-shadow: none;
}
.signin-text{
  padding-top: 80px;
  padding-bottom: 20px;
}
.otp-page{
  padding-top: 40px;
  padding-bottom: 20px;
}
.product-image{
  width: 100px;
  height: 250px;
}
.footer{
   background: #dbdbdb;
  padding-bottom: 20px;
}
.footer-color{
  color: #4a4a4a;
}
.btn-style{
  margin-right: 10px;
}

.progresy-ul{
  text-align: center;
}
.progresy-ul li{
  display: inline-block;
  width: 200px;
  position: relative;
}
.progresy-ul li .fa{
  background: #43A047;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  padding: 3px;
  font-size: 12px;
}
.progresy-ul li .fa::after{
  content: '';
  background: #ccc;
  height: 2px;
  width: 92px;
  display: block;
  position: absolute;
  left: 0;
  top: 12px;
  z-index: 1;
}
.progresy-ul li:nth-child(1) .fa::after,
.progresy-ul li:nth-child(2) .fa::after{
  background: #43A047;
}
.progresy-ul li:first-child .fa::after{
  width: 105px;
  left: 108px;
}

@media (max-width: 600px){
  .progresy-ul li{
    display: inline-block;
    width: 95px;
    position: relative;
  }
 .progresy-ul i .fa::after{
    content: '';
    background: #ccc;
    height: 2px;
    width: 10px;
    display: block;
    position: absolute;
    left: 0;
    top: 12px;
    z-index: 1;
  }
 .progresy-ul li:first-child .fa::after{
    width: 50px;
    left: 50px;
  }
  .progresy-ul li:nth-child(2) .fa::after{
    width: 40px;
    right: 40px;
  }
}

.tabs .tab a{
  color:#006491;
} /*Black color to the text*/

.tabs .tab a:hover {
  background-color:#eee;
  color:#006491;
} /*Text color on hover*/

.tabs .tab a.active {
  background-color:#d2eaf5 !important;
  color:#006491;
} /*Background and text color when a tab is active*/

.tabs .indicator {
  background-color: #006491;
} 
